  width: 80px;
  height: 80px;
}
.ModalX .lds-loader div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: black;
  -webkit-animation: lds-loader 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
          animation: lds-loader 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.ModalX .lds-loader div:nth-child(1) {
  left: 8px;
  -webkit-animation-delay: -0.24s;
          animation-delay: -0.24s;
}
.ModalX .lds-loader div:nth-child(2) {
  left: 32px;
  -webkit-animation-delay: -0.12s;
          animation-delay: -0.12s;
}
.ModalX .lds-loader div:nth-child(3) {
  left: 56px;
  -webkit-animation-delay: 0;
          animation-delay: 0;
}
@-webkit-keyframes lds-loader {
